import React, { useEffect, useState } from "react";
import { Tabs } from "antd";
import FileTabel from "./FileTable";

const { TabPane } = Tabs;

const TabsIndex = ({ setFileSelect, tabIndex, setTabIndex, uploadCount }) => {
  const callback = (key) => {
    setTabIndex(key);
  };
  return (
    <Tabs onChange={callback} type="card">
      <TabPane tab="mp3" key="1">
        <FileTabel
          setFileSelect={setFileSelect}
          tabIndex={tabIndex}
          uploadCount={uploadCount}
        />
      </TabPane>
      <TabPane tab="video" key="2">
        <FileTabel
          setFileSelect={setFileSelect}
          tabIndex={tabIndex}
          uploadCount={uploadCount}
        />
      </TabPane>
    </Tabs>
  );
};

export default TabsIndex;
