const MainModalIndex = ({ openModal, setOpenModal }) => {
  // 열기, 닫기, 모달 헤더 텍스트를 부모로부터 받아옴
  console.log("openModal >>> : ", openModal);
  return (
    // 모달이 열릴때 openModal 클래스가 생성된다.
    <div className={openModal ? "openModal modal" : "modal"}>
      {openModal ? (
        <section>
          <header>
            asdfwefwefwef
            <button className="close" onClick={() => setOpenModal(!openModal)}>
              &times;
            </button>
          </header>
          <main>awrewegwefwe</main>
          <footer>
            <button className="close" onClick={() => setOpenModal(!openModal)}>
              close
            </button>
          </footer>
        </section>
      ) : null}
    </div>
  );
};

export default MainModalIndex;
