import React, { useEffect, useRef, useState } from "react";
import { Card, Button, Statistic } from "antd";
import styled from "styled-components";
import TabsIndex from "./Tabs";
import InputIndex from "./InputField";
import axios from "axios";
import moment from "moment";
import MainModalIndex from "../modal/MainModal";
import io from "socket.io-client";

const socket = io("http://localhost:3001/");
let filePath;
let alarmSet = true;
let alarmFilePath;
let alarmTime;
let code;
const BodyIndex = () => {
  const [userName, setUserName] = useState("been");
  const [fileSelect, setFileSelect] = useState({
    fileName: "",
    fileUrl: "",
    type: "",
  });
  const interval = useRef();
  const [time, setTime] = useState(moment());
  const [openModal, setOpenModal] = useState(false);
  const [tabIndex, setTabIndex] = useState("1");
  const [modalCount, setModalCount] = useState(0);
  const [alarmState, setAlarmState] = useState({
    fileUrl: "",
    fileName: "",
    time: "",
  });
  const [gubCode, setGubCode] = useState();
  const [uploadCount, setUploadCount] = useState(0);
  useEffect(() => {
    filePath = `${fileSelect?.fileUrl}/${fileSelect?.fileName}`;
    code = gubCode;
  }, [fileSelect]);
  useEffect(() => {
    alarmFilePath = alarmState?.fileUrl
      ? `${alarmState?.fileUrl}/${alarmState?.fileName}`
      : "servers/mp3/sound.mp3";
    alarmTime = alarmState.time;
    code = gubCode;
  }, [alarmState]);
  useEffect(() => {
    interval.current = setInterval(() => {
      alarm();
    }, 50);
    return () => {
      clearInterval(interval.current);
    };
  }, []);

  const alarm = async () => {
    setTime(moment());

    if (alarmTime === moment().format("YYYY년MM월DD일-HH-mm-ss")) {
      if (alarmSet) {
        alarmSet = false;

        try {
          const res = await axios.get("http://localhost:3001/mp3", {
            params: { filePath: code === 2 ? alarmFilePath : filePath },
          });
          var audioSrc = "data:audio/mp3;base64," + res.data.fileContent;
          var audio = new Audio();
          audio.src = audioSrc;
          audio.load();
          audio.play();
          if (code === 2) {
            socket.emit("secondMoniterAlarmData", {
              params: {
                musicStart: true,
              },
            });
          } else {
            socket.emit("secondMoniterData", {
              params: {
                type: fileSelect.type,
                value: {
                  fileName: fileSelect.fileName,
                  fileUrl: fileSelect.fileUrl,
                },
                musicStart: false,
              },
            });
          }
        } catch (error) {}
        // const audio = new Audio(sound);
        // audio.play();      }
      }
    } else {
      alarmSet = true;
    }
  };

  useEffect(() => {
    socket.emit("roomjoin", userName);
  }, [fileSelect]);
  return (
    <>
      <StyledCard>
        <Button
          type="primary"
          style={{ float: "right" }}
          onClick={() => {
            window.open("/chatting", "_blank");
            window.open("/chatting2", "_blank");
          }}
        >
          채팅창 열기
        </Button>
        <Button
          type="primary"
          style={{ float: "right" }}
          onClick={() => {
            window.open("/chatting2", "_blank");
          }}
        >
          채팅창 열기
        </Button>
        <Statistic
          value={`TIME : ${time.format("YYYY-MM-DD / HH시mm분ss초")}`}
          precision={2}
          valueStyle={{ color: "#3f8600" }}
        />
        <br />
        <TabsIndex
          setFileSelect={setFileSelect}
          tabIndex={tabIndex}
          setTabIndex={setTabIndex}
          uploadCount={uploadCount}
        />
        <br />
        <InputIndex
          fileSelect={fileSelect}
          modalCount={modalCount}
          alarmState={alarmState}
          setAlarmState={setAlarmState}
          setGubCode={setGubCode}
          uploadCount={uploadCount}
          setUploadCount={setUploadCount}
        />
        <br />
        <Button
          type="primary"
          onClick={() => {
            setModalCount(modalCount + 1);
            window.open("/secondMoniter", "_blank");
          }}
        >
          팝업 열기
        </Button>
      </StyledCard>

      {openModal && (
        <MainModalIndex openModal={openModal} setOpenModal={setOpenModal} />
      )}
    </>
  );
};

export default BodyIndex;

const StyledCard = styled(Card)`
  border: 0.5px solid 708090;
  height: 100%;
`;
