import React, { useEffect, useMemo, useState } from "react";
import { Table } from "antd";
import axios from "axios";

const columns = [
  {
    title: "Name",
    dataIndex: "name",
    key: "name",
  },
  {
    title: "Type",
    dataIndex: "type",
    key: "type",
  },
];

const FileTableIndex = ({ setFileSelect, tabIndex, uploadCount }) => {
  const [userName, setUserName] = useState(null);
  const [filesList, setFilesList] = useState();
  const params = {
    filesName: tabIndex === "1" ? "servers/mp3" : "servers/video",
  };
  useEffect(() => {
    const fileListFun = async () => {
      try {
        const res = await axios.get("http://localhost:3001/fileList", {
          params: params,
        });
        setFilesList(res.data.fileList);
      } catch (ex) {
        console.log(ex);
      }
    };
    fileListFun();
  }, [tabIndex, uploadCount]);

  const fileList = useMemo(() => {
    let list = filesList?.map((v, index) => {
      return { name: v, key: index, type: tabIndex === "1" ? "mp3" : "video" };
    });
    return list;
  }, [filesList]);

  const rowSelection = {
    onSelect: (record, selected, selectedRows) => {
      setFileSelect({
        fileName: selected ? selectedRows[0]?.name : "",
        fileUrl: params.filesName,
        type: selectedRows[0]?.type,
      });
    },
  };

  return (
    <Table
      columns={columns}
      rowSelection={rowSelection}
      dataSource={fileList}
      pagination={false}
      scroll={{ y: 240 }}
    />
  );
};

export default FileTableIndex;
