import React, { useEffect, useRef, useState } from "react";
import { Card, Button, Statistic } from "antd";
import styled from "styled-components";

import io from "socket.io-client";

const socket = io("http://localhost:3001/");

const ChattingIndex = () => {
  const nameRef = useRef();
  const subRef = useRef();
  const tRef = useRef();
  const [login, setLogin] = useState();

  const [keyValue, setKeyValue] = useState();
  useEffect(() => {
    socket.emit("ChattingJoin", "whWon");
    socket.on("whWon", (resData) => {
      console.log("resData >>> : ", resData.params.message);

      // 클라이언트1이 누른 버튼-> 서버-> heejewake이벤트
      tRef.current.value = tRef.current.value + "\n" + resData.params.message;
    });
  }, []);
  const checkIn = () => {
    setLogin(nameRef.current.value);
    console.log(nameRef.current.value);
  };

  const subFun = () => {
    setKeyValue("whWon");
    tRef.current.value =
      tRef.current.value + "\n" + "나 : " + subRef.current.value;
    socket.emit("chattWon", {
      params: {
        to: "acWon",
        message: subRef.current.value,
        key: "whWon",
      },
    });
  };
  return (
    <StyledCard>
      <input ref={nameRef}></input>
      <Button
        type="primary"
        onClick={() => checkIn()}
        style={{ marginLeft: "10px" }}
      >
        CheckIn
      </Button>
      <br />
      <br />
      <textarea
        style={{
          width: "60%",
          height: "600px",
          textAlign: keyValue === "whWon" ? "right" : "left",
        }}
        ref={tRef}
        readOnly
      />
      <br />
      <input ref={subRef} style={{ width: "52%", height: "100px" }}></input>

      <Button
        type="primary"
        onClick={() => subFun()}
        style={{ marginLeft: "10px", marginBottom: "0px" }}
      >
        전송
      </Button>
    </StyledCard>
  );
};

export default ChattingIndex;

const StyledCard = styled(Card)`
  border: 0.5px solid 708090;
  height: 100%;
`;
