import React, { useEffect, useState } from "react";
import { Layout, Menu } from "antd";
import styled from "styled-components";
import { UserOutlined, HomeOutlined } from "@ant-design/icons";
import LayOutHeader from "../headerComponent/Header";
import LayOutBody from "../bodyComponent/Body";
import LayOutFooter from "../footerComponent/Footer";
import axios from "axios";
const { Header, Content, Footer, Sider } = Layout;

function MainMoniterIndex() {
  return (
    <Layout>
      <Layout>
        <StyledHeader>
          <LayOutHeader />
        </StyledHeader>
        <StyledContent>
          <LayOutBody />
        </StyledContent>
        <Footer style={{ textAlign: "center" }}>
          <LayOutFooter />
        </Footer>
      </Layout>
    </Layout>
  );
}

export default MainMoniterIndex;

const StyledHeader = styled(Header)`
  padding: 0;
  text-align: center;
  font-weight: bold;
  font-size: 20px;
  -webkit-box-shadow: 2px 1px 5px 0px rgba(0, 0, 0, 0.26);
  -moz-box-shadow: 2px 1px 5px 0px rgba(0, 0, 0, 0.26);
  box-shadow: 2px 1px 5px 0px rgba(0, 0, 0, 0.26);
  background: #f0f3f4;
`;

const StyledContent = styled(Content)`
  height: 100vh;
  padding: 10px;
  overflow: initial;
`;
