import React, { useEffect, useRef, useState } from "react";
import styled from "styled-components";
import TabsIndex from "./Tabs";
import { Input, Select, Icon, Radio, Card, Button, Tabs, Calendar } from "antd";
import axios from "axios";
import io from "socket.io-client";
import moment from "moment";
import _ from "lodash";

const { TabPane } = Tabs;

const socket = io("http://localhost:3001/");
const { Option } = Select;
const InputField = ({
  fileSelect,
  modalCount,
  alarmState,
  setAlarmState,
  setGubCode,
  uploadCount,
  setUploadCount,
}) => {
  const [inputValue, setInputValue] = useState();
  const [file, setFile] = useState();
  const [fileName, setFileName] = useState("");
  const [filesName, setFilesName] = useState("");
  const interval = useRef();
  const [rodioValue, setRodioValue] = useState(1);
  const [tabIndex, setTabIndex] = useState(1);
  const [calendarValue, setCalendarValue] = useState();
  const [timeState, setTimeState] = useState({
    hour: "00",
    minute: "00",
    second: "00",
  });
  const timeRef = useRef();

  useEffect(() => {
    setInputValue(fileSelect.fileName);
  }, [fileSelect.fileName]);

  const onChange = (e) => {
    setRodioValue(e.target.value);
  };

  const saveFile = (e, filesName) => {
    setFile(e.target?.files[0]);
    setFileName(e.target?.files[0]?.name);
    console.log("filesName >>> : ", filesName);
    setFilesName(filesName);
  };

  const uploadFile = async (e) => {
    const formData = new FormData();
    formData.append("file", file);
    formData.append("fileName", fileName);
    formData.append("filesName", filesName);
    console.log(file, fileName, filesName);

    try {
      const res = await axios.post("http://localhost:3001/upload", formData);
      console.log(res);
    } catch (ex) {
      console.log(ex);
    }
    console.log(uploadCount);
    setUploadCount(uploadCount + 1);
  };

  const soketSub = (e) => {
    // if (modalCount === 0) {
    //   alert("팝업을 띄워 주십시오.");
    //   return;
    // }
    setGubCode(1);
    if (!fileSelect.fileName) {
      alert("파일을 선택해 주십시오.");
      return;
    }
    socket.emit("secondMoniterData", {
      params: {
        type: fileSelect.type,
        value: { fileName: fileSelect.fileName, fileUrl: fileSelect.fileUrl },
        musicStart: false,
      },
    });
  };
  const callback = (key) => {
    setTabIndex(key);
  };
  const alarmFum = () => {
    setGubCode(2);
    setAlarmState({
      time: timeRef.current.value,
      fileName: fileSelect.fileName,
      fileUrl: fileSelect.fileUrl,
    });
  };
  const calendarOnChange = (value) => {
    setCalendarValue(moment(value).format("YYYY년MM월DD일"));
    timeRef.current.value =
      moment(value).format("YYYY년MM월DD일") +
      `-${timeState.hour}-${timeState.minute}-${timeState.second}`;
  };
  const alarmTimeFun = (time, uNd) => {
    let { hours, minutes, seconds } = {
      hours: timeState.hour > 0 ? timeState.hour : "00",
      minutes: timeState.minute > 0 ? timeState.minute : "00",
      seconds: timeState.second > 0 ? timeState.second : "00",
    };
    if (time === "h") {
      if (uNd === "u") {
        let setTime = timeState.hour === "24" ? 0 : Number(timeState.hour) + 1;
        setTime = setTime < 10 ? "0" + setTime : "" + setTime;
        setTimeState({
          ...timeState,
          hour: setTime,
        });
        hours = setTime;
      } else {
        let setTime = timeState.hour === "0" ? 24 : Number(timeState.hour) - 1;
        setTime = setTime < 10 ? "0" + setTime : "" + setTime;
        setTimeState({
          ...timeState,
          hour: setTime,
        });
        hours = setTime;
      }
    }
    if (time === "m") {
      if (uNd === "u") {
        let setTime =
          timeState.minute === "60" ? 0 : Number(timeState.minute) + 1;
        setTime = setTime < 10 ? "0" + setTime : "" + setTime;

        setTimeState({
          ...timeState,
          minute: setTime,
        });
        minutes = setTime;
        console.log("setTime >>> : ", setTime);
      } else {
        let setTime =
          timeState.minute === "0" ? 60 : Number(timeState.minute) - 1;
        setTime = setTime < 10 ? "0" + setTime : "" + setTime;
        setTimeState({
          ...timeState,
          minute: setTime,
        });
        minutes = setTime;
      }
    }
    if (time === "s") {
      if (uNd === "u") {
        let setTime =
          timeState.second === "60" ? 0 : Number(timeState.second) + 1;
        setTime = setTime < 10 ? "0" + setTime : "" + setTime;
        setTimeState({
          ...timeState,
          second: setTime,
        });
        seconds = setTime;
      } else {
        let setTime =
          timeState.second === "0" ? 60 : Number(timeState.second) - 1;
        setTime = setTime < 10 ? "0" + setTime : "" + setTime;
        setTimeState({
          ...timeState,
          second: setTime,
        });
        seconds = setTime;
      }
    }

    const s = _.isEmpty(calendarValue) ? "" : `${calendarValue}-`;
    timeRef.current.value = s + hours + "-" + minutes + "-" + seconds;
  };
  return (
    <Tabs onChange={callback} type="card">
      <TabPane tab="파일" key="1">
        <StyledCard>
          <Radio.Group onChange={onChange} value={rodioValue}>
            <Radio value={1}>mp3 파일 올리기</Radio>
            <Radio value={2}>video 파일 올리기</Radio>
            <Radio value={3}>전송</Radio>
          </Radio.Group>
          <br />
          <br />
          {rodioValue === 1 || rodioValue === 2 ? (
            <>
              {rodioValue === 1 ? (
                <input type="file" onChange={(e) => saveFile(e, "mp3")} />
              ) : (
                <input type="file" onChange={(e) => saveFile(e, "video")} />
              )}

              <button onClick={uploadFile}>Upload</button>
            </>
          ) : (
            <>
              <Input
                defaultValue=""
                value={inputValue}
                style={{ width: "50%" }}
              />
              <Button
                type="primary"
                onClick={() => soketSub()}
                style={{ marginLeft: "10px" }}
              >
                전송
              </Button>
            </>
          )}
        </StyledCard>
      </TabPane>
      <TabPane tab="알람 설정" key="2">
        <StyleDiv className="site-calendar-demo-card">
          <Calendar
            fullscreen={false}
            onPanelChange={() => calendarOnChange()}
            onChange={(e) => calendarOnChange(e)}
          />
        </StyleDiv>
        <div style={{ float: "left", marginLeft: "20px" }}>
          <br />
          <span style={{ float: "left", marginTop: "12px" }}>시</span>
          <div style={{ float: "left" }}>
            <Button
              type="primary"
              onClick={() => alarmTimeFun("h", "u")}
              style={{
                marginLeft: "10px",
                height: "15px",
                width: "20px",
                background: "gray",
                border: "1px solid gray",
              }}
            >
              ▲
            </Button>
            <br />
            <Button
              type="primary"
              onClick={() => alarmTimeFun("h", "d")}
              style={{
                marginLeft: "10px",
                height: "15px",
                width: "20px",
                marginTop: "-300px",
                background: "gray",
                border: "1px solid gray",
              }}
            >
              ▼
            </Button>
          </div>
        </div>
        <div style={{ float: "left", marginLeft: "10px" }}>
          <br />
          <span style={{ float: "left", marginTop: "12px" }}>분</span>
          <div style={{ float: "left" }}>
            <Button
              type="primary"
              onClick={() => alarmTimeFun("m", "u")}
              style={{
                marginLeft: "10px",
                height: "15px",
                width: "20px",
                background: "gray",
                border: "1px solid gray",
              }}
            >
              ▲
            </Button>
            <br />
            <Button
              type="primary"
              onClick={() => alarmTimeFun("m", "d")}
              style={{
                marginLeft: "10px",
                height: "15px",
                width: "20px",
                marginTop: "-300px",
                background: "gray",
                border: "1px solid gray",
              }}
            >
              ▼
            </Button>
          </div>
        </div>
        <div style={{ float: "left", marginLeft: "20px" }}>
          <br />
          <span style={{ float: "left", marginTop: "12px" }}>초</span>
          <div style={{ float: "left" }}>
            <Button
              type="primary"
              onClick={() => alarmTimeFun("s", "u")}
              style={{
                marginLeft: "10px",
                height: "15px",
                width: "20px",
                background: "gray",
                border: "1px solid gray",
              }}
            >
              ▲
            </Button>
            <br />
            <Button
              type="primary"
              onClick={() => alarmTimeFun("s", "u")}
              style={{
                marginLeft: "10px",
                height: "15px",
                width: "20px",
                marginTop: "-300px",
                background: "gray",
                border: "1px solid gray",
              }}
            >
              ▼
            </Button>
          </div>
        </div>
        <br />
        <br />
        <br />
        <br />
        <input
          type="text"
          ref={timeRef}
          style={{ width: "350px", marginLeft: "20px" }}
        />

        <Button
          type="primary"
          onClick={() => alarmFum()}
          style={{ marginLeft: "10px" }}
        >
          설정
        </Button>
      </TabPane>
    </Tabs>
  );
};

export default InputField;

const StyledCard = styled(Card)`
  border: 0.5px solid 708090;
  height: 100%;
`;

const StyleDiv = styled.div`
  border: 1px solid #f0f0f0;
  width: 300px;
  borderradius: 2px;
  float: left;
`;
