import React, { useEffect, useRef, useState } from "react";
import io from "socket.io-client";
import Video from "./Video";
import Music from "./Music";
import BasicView from "./BasicView";
import { Modal, Button } from "antd";
import _ from "lodash";
const socket = io("http://localhost:3001/");
const SecondMoniterIndex = () => {
  const [actionType, setActionType] = useState();
  const [isModalVisible, setIsModalVisible] = useState(false);
  const [alarmVisible, setAlarmVisible] = useState(false);
  const showModal = () => {
    setIsModalVisible(true);
  };

  const handleOk = () => {
    setIsModalVisible(false);
  };

  const handleCancel = () => {
    setIsModalVisible(false);
  };

  useEffect(() => {
    //   if (!actionType?.params?.type) alert("항목을 선택해 주십시오.");
    socket.emit("roomjoin", ["secondMoniter", "secondMoniterAlarm"]);
    socket.on("secondMoniter", (resData) => {
      // 클라이언트1이 누른 버튼-> 서버-> heejewake이벤트
      setActionType(resData); // 에서 메시지 hwi를 받는다
    });
    socket.on("secondMoniterAlarm", (resData) => {
      // 클라이언트1이 누른 버튼-> 서버-> heejewake이벤트
      console.log("resData >>> : ", resData);
      setAlarmVisible(resData?.params?.musicStart);
    });
  }, []);

  return (
    <>
      {console.log("alarmVisible >>> : ", alarmVisible)}
      <Modal
        title="Basic Modal"
        visible={alarmVisible}
        onOk={() => setAlarmVisible(!alarmVisible)}
        onCancel={() => setAlarmVisible(!alarmVisible)}
      >
        <p>Some contents...</p>
        <p>Some contents...</p>
        <p>Some contents...</p>
      </Modal>

      {actionType?.params?.type ? (
        actionType?.params?.type === "mp3" ? (
          <Music actionType={actionType} />
        ) : (
          <Video actionType={actionType} />
        )
      ) : (
        <BasicView />
      )}
    </>
  );
};

export default SecondMoniterIndex;
